___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --text-color: #393E41;
  --emphasis-color: #E94F37;
  --background-color: #F6F7EB;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

button,
input {
  font: inherit;
}

body {
  min-height: 100vh;
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Open Sans', sans-serif;
  margin: 0;
}

h1, h2 {
  color: var(--emphasis-color);
}

/*HEADER*/

header {
  padding: 32px;
}

nav {
  display: flex;
  justify-content: center;
  gap: 16px;
}

.link {
  --link-color: var(--text-color);
  background: none;
  border: none;
  cursor: pointer;
  color: var(--link-color);
  font-weight: 600;
  font-size: 1.5rem;
  position: relative;
  transition: color 0.2s;
}

.link::before {
  content: "";
  position: absolute;
  height: 1px;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: var(--link-color);
  transform: scaleX(0);
  transition: transform 0.3s, background-color 0.2s;
}

.link.active,
.link.active::before {
  --link-color: var(--emphasis-color);
}

.link.active::before, .link:hover::before {
  transform: scaleX(1);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,qBAAqB;EACrB,yBAAyB;EACzB,2BAA2B;AAC7B;;AAEA;;;EAGE,sBAAsB;AACxB;;AAEA;;EAEE,aAAa;AACf;;AAEA;EACE,iBAAiB;EACjB,yCAAyC;EACzC,wBAAwB;EACxB,oCAAoC;EACpC,SAAS;AACX;;AAEA;EACE,4BAA4B;AAC9B;;AAEA,SAAS;;AAET;EACE,aAAa;AACf;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,SAAS;AACX;;AAEA;EACE,+BAA+B;EAC/B,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,wBAAwB;EACxB,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;EAClB,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,WAAW;EACX,WAAW;EACX,SAAS;EACT,OAAO;EACP,mCAAmC;EACnC,oBAAoB;EACpB,iDAAiD;AACnD;;AAEA;;EAEE,mCAAmC;AACrC;;AAEA;EACE,oBAAoB;AACtB","sourcesContent":[":root {\n  --text-color: #393E41;\n  --emphasis-color: #E94F37;\n  --background-color: #F6F7EB;\n}\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nbutton,\ninput {\n  font: inherit;\n}\n\nbody {\n  min-height: 100vh;\n  background-color: var(--background-color);\n  color: var(--text-color);\n  font-family: 'Open Sans', sans-serif;\n  margin: 0;\n}\n\nh1, h2 {\n  color: var(--emphasis-color);\n}\n\n/*HEADER*/\n\nheader {\n  padding: 32px;\n}\n\nnav {\n  display: flex;\n  justify-content: center;\n  gap: 16px;\n}\n\n.link {\n  --link-color: var(--text-color);\n  background: none;\n  border: none;\n  cursor: pointer;\n  color: var(--link-color);\n  font-weight: 600;\n  font-size: 1.5rem;\n  position: relative;\n  transition: color 0.2s;\n}\n\n.link::before {\n  content: \"\";\n  position: absolute;\n  height: 1px;\n  width: 100%;\n  bottom: 0;\n  left: 0;\n  background-color: var(--link-color);\n  transform: scaleX(0);\n  transition: transform 0.3s, background-color 0.2s;\n}\n\n.link.active,\n.link.active::before {\n  --link-color: var(--emphasis-color);\n}\n\n.link.active::before, .link:hover::before {\n  transform: scaleX(1);\n}"],"sourceRoot":""}]);
